<template>
  <van-popup v-model="showModal" get-container="#app-warp" position="bottom" class="custom-share" style="border-radius: 0.24rem 0.24rem 0 0;">
    <div class="share-wrapper">
      <!-- <div class="title-line">您的好友通过分享购买，您将赚到不菲的佣金</div> -->
      <ul class="share-way-list">
        <li v-for="item of shareWayList" class="share-item" :key="item.label" @click="shareHandler(item.params)">
          <div class="icon-wrapper">
            <img :src="item.img" alt="">
          </div>
          <div class="share-name">{{item.label}}</div>
        </li>
      </ul>
    </div>
  </van-popup>
</template>

<script>
import './custom-share.scss'
import ClipboardJS from 'clipboard';
import UplusApi from '@uplus/uplus-api'
import { checkIdentity } from '@/http/service/shop'
import { myManageinfo } from '@/http/service/mine'
import utils from '@/utils/utils'

const instance = new UplusApi()
instance.initDeviceReady()

const shareWayMap = {
  WechatSession: {
    img: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/share/wx-icon3x.png',
    label: '微信',
    params: { platformType: 'WechatSession' }
  },
  WechatTimeLine: {
    img: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/share/wx-time-line-icon3x.png',
    label: '朋友圈',
    params: { platformType: 'WechatTimeLine' }
  },
  QQ: {
    img: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/share/qq-icon3x.png',
    label: 'QQ',
    params: { platformType: 'QQ' }
  },
  QQzone: {
    img: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/share/qq-zone-icon3x.png',
    label: 'QQ空间',
    params: { platformType: 'QQzone' }
  },
  Sina: {
    img: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/share/sina-icon3x.png',
    label: '微博',
    params: { platformType: 'Sina' }
  },
  CopyLink: {
    img: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/share/link-icon3x.png',
    label: '复制链接',
    params: { callback: 'linkShare' }
  },
  Picture: {
    img: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/share/picture-icon3x.png',
    label: '画报分享',
    params: { callback: 'pictureShare' }
  }
}

// 根据路由区分不同页面需要不同的分享途径  shopDetail=店铺主页  detail=单品页
const pageFunctions = {
  shopDetail: {
    ways: ['WechatSession', 'WechatTimeLine', 'QQ', 'QQzone', 'Sina', 'CopyLink'],
    params: 'storeId'
  },
  detail: {
    ways: ['WechatSession', 'WechatTimeLine', 'QQ', 'QQzone', 'Sina', 'CopyLink', 'Picture'],
    params: 'productId'
  }
}

// ihaier内分享仅支持【微信，朋友圈】，对pageFunctions进行重置
if (utils.isIhaierApp()) {
  pageFunctions['shopDetail']['ways'] = ['WechatSession', 'WechatTimeLine']
  pageFunctions['detail']['ways'] = ['WechatSession', 'WechatTimeLine']
}

export default {
  components: {},
  props: {
    show: Boolean,
    copyCustomLink: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      showModal: true,
      shareWayList: []
    }
  },
  created () {
    this.getShareWay()
  },
  mounted () {},
  beforeDestroy () {},
  methods: {
    changeStyleIndex () {
      const overlay = document.querySelector('.tab-recommend .van-sticky--fixed')
      if (overlay) {
        overlay.style['z-index'] = this.showModal ? 99 : null
      }
    },
    linkShare() {
      if (this.copyCustomLink) {
        this.$emit('copyLink')
      } else {
        const a = document.createElement('a')
        const clipboard = new ClipboardJS(a, {
          text: function (trigger) {
            return window.location.href
          }
        });
        clipboard.on('success', () => {
          this.$toast('复制链接成功');
        })
        clipboard.on('error', () => {
          this.$toast('复制链接失败');
        })
        a.click()
      }
    },
    pictureShare() {
      this.$emit('pictureShare')
    },
    shareHandler (params) {
      if (params.callback) {
        this[params.callback]()
      } else {
        if (this.$route.name === 'detail') {
          this.$emit('customShare', { platformType: params.platformType })
        }
        if (this.$route.name === 'shopDetail') {
          this.$emit('customShare', { platformType: params.platformType })
        }
      }
      this.showModal = false
    },
    getShareWay () {
      if (pageFunctions[this.$route.name]) {
        this.shareWayList = pageFunctions[this.$route.name].ways.map((way) => {
          return shareWayMap[way]
        })
        // ihaier内不展示画报分享
        if (this.$route.name === 'shopDetail' && !utils.isIhaierApp()) {
          this.getShareAuth()
        }
      }
    },
    // ajax
    getShareAuth () {
      if (!this.$store.getters.getLoginStatus) {
        this.shareWayList.push(shareWayMap['Picture'])
        return
      }
      const promiseList = [myManageinfo(), checkIdentity({ storeOwnerId: this.$route.query.storeId })]
      Promise.all(promiseList).then((values) => {
        let isPartner = false
        if (values[0].data.success) {
          if (values[0].data.data.identifyFlag && [22, 21, 31].includes(values[0].data.data.identifyFlag)) {
            isPartner = true
          }
        }
        if (isPartner && values[1].success) {
          if (!values[1].data) { return }
        }
        this.shareWayList.push(shareWayMap['Picture'])
      })
    }
  },
  computed: {},
  watch: {
    show: {
      handler (val) {
        this.showModal = val
        this.changeStyleIndex()
      },
      immediate: true
    },
    showModal: {
      handler (val) {
        if (!val) {
          this.$emit('update:show', false)
        }
        // val ? this.getShareWay() : this.$emit('update:show', false)
      },
      immediate: true
    }
  }
}
</script>
